import React from 'react';
import styles from './index.module.css';
import Header from "../components/header";
import CryptoSection from "../components/crypto-section";
import WorksSection from "../components/works-section";
import BenefitsSection from "../components/benefits-section";
import SimpleSection from "../components/simple-section";
import ParaSection from "../components/para-section";
import TermsOfService from "../components/terms-of-service";
import Footer from "../components/footer";

class Landing extends React.Component {

    isTermsOfService() {
        const path = localStorage.getItem('path');
        if (path && path.replace("/", "") === "terms-of-service") {
            window.history.pushState("", "", '/terms-of-service');
            localStorage.clear();
            return true;
        };
        return false;
    }
    render() {
        const isTermsOfService = this.isTermsOfService();
        return (
            <>
                {
                    isTermsOfService &&
                    <section className={styles.landing}>
                        <Header />
                        <TermsOfService />
                    </section>
                }
                {
                    !isTermsOfService &&
                    <section className={styles.landing}>
                        <Header />
                        <CryptoSection />
                        <WorksSection />
                        <BenefitsSection />
                        <SimpleSection />
                        <ParaSection />
                        <Footer />
                    </section>
                }
            </>
        );
    }
}

export default Landing;
